import React from "react"
import { Link, graphql } from "gatsby"
import Disqus from 'gatsby-plugin-disqus';
import { FontAwesomeIcon } from '@fortawesome/react-fontawesome'
import Layout from "../components/layout"
import SEO from "../components/seo"
import { rhythm } from "../utils/typography"
import {kebabCase, toLower} from "lodash"

class BlogPostTemplate extends React.Component {
  render() {
    const post = this.props.data.markdownRemark
    const pathName = this.props.location.pathname;
    const siteTitle = this.props.data.site.siteMetadata.title;
    const siteUrl = this.props.data.site.siteMetadata.siteUrl;
    const { previous, next } = this.props.pageContext;
    const { frontmatter } = post;

    return (
      <>
        <Layout location={this.props.location} title={siteTitle}>
          <SEO
            title={frontmatter.title}
            description={frontmatter.description || post.excerpt}
          />
          <div
            className={"d-flex flex-column justify-content-end mt-5 mb-5 pt-5 pb-5"}
            style={{textAlign: 'right'}}
          >
            <div className={"d-flex justify-content-end"}>            
              <h1 style={{maxWidth: rhythm(35), color: '#17a2b8'}}>{frontmatter.title}</h1>
            </div>
            <div className={"d-flex justify-content-end align-items-center"}>
              <div style={{fontSize: '13px', color: '#999999'}}>
                {frontmatter.date}
              </div>
              <div className={"d-flex"}>
                {
                  (frontmatter.tags && frontmatter.tags.length > 0)
                  && (
                    frontmatter.tags.map(tag => (
                      <Link
                        to={`/tags/${kebabCase(toLower(tag))}`}
                        className="badge badge-info pt-1 pb-1 pr-2 pl-2 ml-1"
                        key={tag}
                      >
                        <FontAwesomeIcon icon={'tag'} size="xs" className={"mr-1"} />
                        {tag}
                      </Link>
                    ))
                  )
                }
              </div>
            </div>
          </div>

          <div dangerouslySetInnerHTML={{ __html: post.html }} />

          <div className={"d-flex justify-content-between pt-5 pb-3 mt-5 mb-3"}>
            <div style={{maxWidth: '50%'}}>
              {previous && (
                <>
                  <div style={{fontWeight: 'bold'}}>Next</div>
                  <Link
                    style={{ boxShadow: `none`, textDecoration: `none`, color: `#17a2b8` }}
                    to={previous.fields.slug}
                    rel="prev">
                    {previous.frontmatter.title}
                  </Link>
                </>
              )}
            </div>
            <div style={{maxWidth: '50%'}}>
              {next && (
                <div style={{textAlign:'right'}}>
                  <div style={{fontWeight: 'bold'}}>Prev</div>
                  <Link 
                    style={{ boxShadow: `none`, textDecoration: `none`, color: `#17a2b8` }}
                    to={next.fields.slug}
                    rel="next">
                    {next.frontmatter.title}
                  </Link>
                </div>
              )}
            </div>
          </div>

          <Disqus 
            identifier={post.id}
            title={post.frontmatter.title}
            url={`${siteUrl}${pathName}`}
          />
        </Layout>
      </>
    )
  }
}

export default BlogPostTemplate

export const pageQuery = graphql`
  query BlogPostBySlug($slug: String!) {
    site {
      siteMetadata {
        title
        author
      }
    }
    markdownRemark(fields: { slug: { eq: $slug } }) {
      id
      excerpt(pruneLength: 160)
      html
      frontmatter {
        title
        date(formatString: "YYYY-MM-DD HH:mm:ss")
        tags
      }
    }
  }
`
